<template>
  <div>
    <div class="tac lottery">
      <mt-button 
        type="default" 
        @click.native="lotteryFn">
        <span v-if="!english">抽奖</span>
        <span v-else>Lottery</span>
      </mt-button>
    </div>
    <div class="tac">
      <span @click="protocol">
        <span v-if="!english">查看协议内容</span>
        <span v-else>Protocol content</span>
      </span>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { MessageBox, Toast } from 'mint-ui'
export default {
  data () {
    return {
      confirmTitle: '提示',
      confirmMessage: '确定执行此操作吗？',
      agreementText: ''
    }
  },
  mounted () {
    if (this.lotteryNum === 5) {
      Toast({
        message: '抽奖失败！',
        position: 'middle',
        duration: 2000
      })
      this.$store.dispatch('setLotteryNum', 0)
      this.$store.dispatch('getTransaction', {
        params: {
          page: 0,
          perpage: 10,
          order: 'desc',
          sort: 'years',
          amount: '',
          type: '',
          txhash: ''
        },
        cb: function (data) {
          console.log(data)
        }
      })
    }
    else {
      let n = this.lotteryNum + 1
      this.$store.dispatch('setLotteryNum', n)

    }
  },
  computed: {
    ...mapState({
      english: states => states.english,
      lotteryNum: states => states.lotteryNum,
      transaction: states => states.api.transaction
    })
  },
  components: {
    MessageBox,
    Toast
  },
  methods: {
    lotteryFn () {
      // 点击抽奖，会返回是否上链成功，但并不一定交易成功，之后再查看宠物界面，产生则投资完成
      MessageBox.confirm('您确定花费10个币进行抽奖吗？', '').then(action => {
        // 比较花费的和当前余额，如果余额不够，提示余额不足，如果可以则进行交易
        if (this.balance < 10) {
          Toast({
            message: '余额不足，请充值后再试！',
            position: 'middle',
            duration: 2000
          })
        }
        else {
          // 进行抽奖交易
          this.$store.dispatch('setBalance', this.balance - 10)
          Toast({
            message: '抽奖完成，请稍后查询结果！',
            position: 'middle',
            duration: 2000
          })
          // 更新首页我的熊猫数据
        }
      }).catch(action => {
        // 关闭
      })
    },
    protocol () {
      this.$router.push({name: 'Agreement'})
    }
  }
}

</script>


<style lang="scss" scoped>
.lottery {
  margin: 300px auto 20px;
}
</style>



